<template>

  <div>
    <div>
      <img src="https://www.scfai.edu.cn/newfy/3little.png" width="306" height="837px" style="float: left">
    </div>
    <div style="float: left">
      <div style="float: left;width: 833px;line-height: 68px;color: #333;font-size: 24px;text-align: center;margin-bottom: 10px;">{{goods.title}}</div>
      <div style="text-align: center;font-size: 12px">发布时间：{{goods.publishertime}}         作者：{{goods.publisher}}    来源：校疫情防控应急指挥部</div>
      <div style="float: left;width: 833px;margin-top: 20px;margin-left: 10px">  {{goods.content}}</div>
    </div>
  </div>
</template>

<script>

  export default {
    name: "Detail",
    data() {
      let goodsId = this.$route.query.id
      return {
        user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : null,
        goods: {},
        goodsId: goodsId,
        buyNum: 1,
        comments: [],
        dialogVisible:false
      }
    },
    created() {
      this.request.get('/mediaCoverage/' + this.goodsId).then(res => this.goods = res.data)

      // this.request.get('/orderItem/comment/' + this.goodsId).then(res => this.comments = res.data)
    },
    methods: {
      addCart() {
        if (this.user==null){
          this.dialogVisible=true
        }
        this.request.post('/cart', { goodsId: this.goodsId, num: this.buyNum, userid:this.user.id }).then(res => {
          if (res.code === '200') {
            this.$message.success('加入购物车成功')
          } else {
            this.$message.error(res.msg)
          }
        })
      },
      toLogin(){
        this.dialogVisible=false
        this.$router.push("login")
      },
    }
  }
</script>

